.cardsField {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1000px;
  flex-wrap: wrap;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.logo {
  width: 100%;
  height: 270px;
  background-image: url(../../image/logo.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
